export default {
  /**
   * 
   * @param state
   */
  hasMenuAuthority(state) {
    return resourceId =>
      state.menuResource.findIndex(x => x.id === resourceId) > -1
  },
  /**
   * 
   * @param state
   */
  hasControlAuthority(state) {
    return controlId =>
      state.controlResource.findIndex(x => x.id === controlId) > -1
  }
}
