





































































import { Vue, Component, Prop, Ref } from 'vue-property-decorator'
import { Page } from '@/core/decorators'
import DataForm from '@/shared/components/data-form.vue'
import { OrderService } from '@/services/order.service'
import { RequestParams } from '@/core/http'

@Page({
  name: 'data-form',
  layout: 'workspace'
})
@Component({
  components: {}
})
export default class DataFormDemo extends Vue {
  // 
  @Ref()
  readonly dataForm!: DataForm

  private data = ''

  // 
  private orderService = new OrderService()

  private cascaderData = [
    {
      value: 'zhejiang',
      label: '',
      children: [
        {
          value: 'hangzhou',
          label: ''
        }
      ]
    },
    {
      value: 'jiangsu',
      label: '',
      children: [
        {
          value: 'nanjing',
          label: ''
        }
      ]
    }
  ]

  // 
  private get rules() {
    return {
      username: [
        { required: true, message: this.$t('rules.username_require') }
      ],
      age: [
        {
          min: 15,
          type: 'number',
          message: this.$t('rules.age_min')
        },
        {
          max: 40,
          type: 'number',
          message: this.$t('rules.age_max')
        }
      ]
    }
  }

  private mounted() {}

  /**
   * 
   */
  private getOrderList() {
    this.dataForm
      .validateFields()
      .then(values => {
        this.orderService
          .getOrderList(new RequestParams(values))
          .subscribe(data => {
            this.data = data
          })
      })
      .catch(err => {
        // 
      })
  }
}
